.header1{
    border-bottom: solid rgb(219, 219, 219) 1px;
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 60px;
    color: grey;
}
.topright{
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 15px;
    font-size: small;
}
.header2{
    padding: 0 50px;
    height: 100%;
    display: flex;
    justify-content: space-between;
}
.header3{
    background: grey;
    height: 40px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.ul{
    display: flex;
    justify-content: space-around;
    margin: 0;
    padding: 0;
    list-style-type: none;
    color: white;
    width: 50%;
}
#search{
    background: white;
   height: 100%;
   width: 80px;
   border: none;
   padding: 0 10px;
   outline: none;
}

#logo1{
    height: 80px;
}