.bottom1{
    background: rgb(225, 225, 225);
    min-width: 20%;
    border-radius: 10px;
    height: 35vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px 10px;

}
.bottom1>form{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px 60px;
}
.bottom1>p{
    margin-top: 30px;
}

.input{
    border: none;
    border-bottom: solid black 1px;
    background: rgb(225, 225, 225);
    padding: 5px 0px;
    margin-bottom: 10px;
    outline: none;
}

.buttons{
    display: flex;
    flex-direction: row;
    column-gap: 10px;
    justify-content: center;
    align-items:center;
    width: 100%;
    height: 40px;
    margin-top: 10px;
    margin-bottom: 20px;

}
.buttons>button:nth-child(1){
    background: rgb(75, 180, 255);
    border: none;
    border-radius: 5px;
    padding: 10px 10px;
}
.buttons>button:nth-child(1):hover{
    background: rgb(75, 180, 255);
    border: none;
    border-radius: 5px;
    padding: 10px 10px;
    cursor: pointer;
}
.buttons>button:nth-child(3):hover{
    background: rgb(255, 127, 127);
    border: none;
    border-radius: 5px;
    padding: 10px 10px;
    cursor: pointer;
}
.buttons>button:nth-child(3){
    background: rgb(255, 127, 127);
    border: none;
    border-radius: 5px;
    padding: 10px 10px;
}
.bottom1>a{
    font-size: x-small;
    color: black;
}